.youtube-logo {
  width: 200px;
  height: 200px;
  background-image: url("https://yt3.ggpht.com/ytc/AAUvwnhEvcqvrxHv9WBTw0l43XAUoq2-uEf_VmXxNQDr-A=s900-c-k-c0x00ffffff-no-rj");
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 20px;
  margin-left: 120px;
  margin-right: 40px;
}
.youtube-clone {
  font-weight: 200;
  font-size: 40px;
  margin-right: 20px;
}
.content {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
  padding-left: 60px;
  font-weight: 200;
  font-size: 30px;
  border-left: 0.5px solid white;
}
.content .span-title {
  font-weight: 400;
  font-size: 30px;
}
.youtube-sourcecode {
  background-color: #050807;
  border-radius: 40px;
  padding: 14px;
  font-size: 25px;
  margin: 10px;
}
.youtube-works {
  background-color: #050807;
  border-radius: 40px;
  padding: 14px;
  font-size: 25px;
  margin: 10px;
}
.next {
  position: absolute;
  bottom: 10px;
  right: 30px;
  font-size: 30px;
  color: white;
  display: flex;
  align-items: center;
}
.next i {
  margin-bottom: 5px;
}
.next span {
  font-size: 20px;
  font-weight: 200;
  padding-bottom: 6px;
  padding-right: 10px;
}
