@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200;300;400;600&display=swap);
header {
  width: 100vw;
  height: 14vh;
  font-size: 40px;
  display: flex;
  align-items: center;
  padding: 10px;
}

header .header-title {
  width: 50%;
  font-weight: 400;
  font-size: 40px;
  display: flex;
  justify-content: center;
}
header .menu {
  width: 50%;
  display: flex;
  justify-content: center;
  font-size: 18px;
}
header .menu .about {
  padding: 0px 40px;
}
header .menu .stacks {
  padding: 0px 40px;
}
@media screen and (max-width: 700px) {
  header .menu .about {
    padding: 0px 5px;
  }
  header .menu .stacks {
    padding: 0px 5px;
  }
}

.title {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 58px;
  font-weight: 200;
  margin-top: 100px;
}
.summary {
  font-size: 30px;
}

@keyframes appear {
  from {
    background-color: rgba(172, 172, 172, 0);
  }
  to {
    background-color: rgba(172, 172, 172, 0.2);
  }
}
@keyframes appearText {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.stack-container {
  width: 88vw;
  height: 78vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 200px;
  animation: appearText 1s ease-in-out forwards;
}
.stack {
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #040707;
  margin: 10px 10px;
}
.nodejs {
  width: 210px;
  height: 210px;
  border-radius: 10px;
  background-image: url("https://pluralsight2.imgix.net/paths/images/nodejs-45adbe594d.png");
  background-size: contain;
  background-repeat: no-repeat;
}
.react {
  width: 210px;
  height: 210px;
  border-radius: 10px;
  background-image: url("https://miro.medium.com/max/500/1*cPh7ujRIfcHAy4kW2ADGOw.png");
  background-size: 70% 70%;
  background-repeat: no-repeat;
  background-position: center;
}
.python {
  width: 210px;
  height: 210px;
  border-radius: 10px;
  background-image: url("https://www.python.org/static/opengraph-icon-200x200.png");
  background-size: 85% 85%;
  background-repeat: no-repeat;
  background-position: center;
}
.unity {
  width: 210px;
  height: 210px;
  border-radius: 10px;
  background-image: url("https://unity3d.com/profiles/unity3d/themes/unity/images/pages/branding_trademarks/unity-tab-square-black.png");
  background-size: contain;
  background-repeat: no-repeat;
}
@media screen and (max-width: 1050px) {
  .stack-container {
    margin-top: 200px;
  }
  .stack {
    margin: 5px;
  }
  .nodejs {
    width: 70px;
    height: 70px;
  }
  .react {
    width: 70px;
    height: 70px;
  }
  .python {
    width: 70px;
    height: 70px;
  }
  .unity {
    width: 70px;
    height: 70px;
  }
}

