body {
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  background-color: #0c0a0a;
  color: white;

  font-family: "Titillium Web", sans-serif;
}
a {
  text-decoration: none;
}
* {
  box-sizing: border-box;
}
#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#no-mobile {
  position: absolute;
  z-index: 99;
  height: 100vh;
  background-color: #1e1f21;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  font-size: 32px;
  color: white;
}
#no-mobile .least {
  font-weight: 200;
  font-size: 20px;
}

#no-mobile {
  visibility: hidden;
}
@media screen and (max-width: 700px) {
  .title {
    font-size: 24px;
  }
  .summary {
    font-size: 12px;
  }
}

